
// NODE MODULES
import React from 'react';
import { connect } from 'react-redux';

// APIS
import votifyServer from '../api/votifyServer';

// ACTIONS
import actions from '../actions/actions';

// CSS
import '../styles/components/PasswordResetForm.css';

class PasswordResetForm extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            newPassword: '',
            newPasswordVerification: ''
        };

        this.onPasswordSend = this.onPasswordSend.bind(this);
    }

    // AUTH FUNCTIONS
    async onPasswordSend({ newPassword, newPasswordVerification }) {
        return () => {

        }
    }

    // RENDER FUNCTIONS
    renderAuthInfo(message) {
        if (message) {
            return (
                <div className="password-reset-form__info">
                    <p>{message}</p>
                </div>
            );
        };

        return null;
    }

    renderForm() {
        return (
            <div className="password-reset-form__form">

                <div>
                    {this.renderAuthInfo(this.props.authInfo)}
                </div>

                <div>
                    <input 
                        placeholder="Password..."
                        type="password"
                        value={this.state.newPassword}
                        onChange={(event) => {
                            this.setState({ ...this.state, newPassword: event.target.value });
                        }}
                    />
                </div>

                <div>
                    <input
                        placeholder="Password Verify..."
                        type="password"
                        value={this.state.newPasswordVerification}
                        onChange={(event) => {
                            this.setState({ ...this.state, newPasswordVerification: event.target.value });
                        }}
                    />
                </div>

                <div>
                    <button
                        onClick={() => {
                            this.props.resetPassword({ 
                                newPassword: this.state.newPassword, 
                                newPasswordVerification: this.state.newPasswordVerification 
                            });
                        }}
                    >
                        Reset Password
                    </button>
                </div>

            </div>
        );
    };

    componentDidMount() {

    }

    render() {
        return (
            <div className="password-reset-form__container">
                <div className="password-reset-form__content">
                    {this.renderForm()}
                </div>
            </div>
        );
    };
}

function mapStateToProps(state) {
    return {
        authInfo: state.auth.authInfo,
        loading: state.auth.loading
    }
}

function mapDispatchToProps(dispatch) {
    return {
        resetPassword: async ({ newPassword, newPasswordVerification }) => {
            try {

                dispatch(actions.loading(true));

                if (!newPassword || !newPasswordVerification) {
                    dispatch(actions.authInfo({ authInfo: 'Please fill all the credentials' }));
                }

                const url = window.location.href;
                const response = await votifyServer.post(url, { password: newPassword, passwordVerification: newPasswordVerification });
                const { data } = response;
        
                if (data.success) {
                    dispatch(actions.authInfo({ authInfo: data.msg }));
                }

                dispatch(actions.loading(false));

            } catch (error) {
                dispatch(actions.loading(false));
                dispatch(actions.authInfo({ authInfo: 'Something went wrong, please try again'}));
            }

            dispatch(actions.loading(false));
    
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PasswordResetForm);

